.pc-nav__items {
  height: inherit;
  display: flex;
  align-items: center;
  margin: 0 0 3.125rem 0;
  list-style: none;
}

.pc-nav__items li a {
  /*線の基点とするためrelativeを指定*/
  position: relative;
  text-align: center;
}

.pc-nav__items li a::after {
  content: "";
  /*絶対配置で線の位置を決める*/
  position: absolute;
  bottom: 0;
  left: 10%;
  bottom: 25%;
  /*線の形状*/
  width: 80%;
  height: 0.125rem;
  background: #fff;
  /*アニメーションの指定*/
  transition: all 0.3s;
  transform: scale(0, 1); /*X方向0、Y方向1*/
  transform-origin: center top; /*上部中央基点*/
}

/*現在地とhoverの設定*/
.pc-nav__items li.current a::after,
.pc-nav__items li a:hover::after {
  transform: scale(1, 1); /*X方向にスケール拡大*/
}

