.slide {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.slide-image {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  opacity: 0;
  animation: slider-1 24s linear infinite;
}

.slide-image:nth-child(1) {
  background-image: url("/assets/images/top/TOP-MV(4).png");
  animation-delay: -2s;
}

.slide-image:nth-child(2) {
  background-image: url("/assets/images/top/TOP-MV(2).png");
  animation-delay: 6s;
}

.slide-image:nth-child(3) {
  background-image: url("/assets/images/top/TOP-MV(3).png");
  animation-delay: 14s;
}

.slide-image:nth-child(3) {
  background-image: url("/assets/images/top/TOP-MV(1).png");
  animation-delay: 14s;
}

@keyframes slider-1 {
  0% {
    opacity: 0;
    transform: scale(1);
  }
  4.16% {
    opacity: 1;
  }
  33.33% {
    opacity: 1;
  }
  41.66% {
    opacity: 0;
    transform: scale(1.2);
  }
  100% {
    opacity: 0;
  }
}
