@use "global" as *;

.price {
    background-color: $green2;
    padding-bottom: rem(107);
    margin-top: rem(68);
    padding-top: rem(80);

    @include mq("md") {
    
        padding-bottom: rem(90);
        position: relative;
        padding-top: rem(100);
        margin-top: rem(130);
    }
}

.price::before {
    background-image: url(/assets/images/top/Frame175.png);
    background-repeat: no-repeat;
    background-size: contain;
    content: "";
    height: rem(50);
    position: absolute;

    @include mq("md") {
        height: rem(300);
        left: rem(100);
        top: rem(928);
        width: rem(439);
        z-index: 10000;
        left: calc(50% - min(48.5vw, 38.75rem));

    }

}

.price__title-inner {

    margin: 0 auto;


    @include mq("md") {

        margin: 0 auto;


    }
}

.price__inner {
    padding: 0 rem(15);
    margin: 0 auto;
    margin-top: rem(7);
    padding-bottom: rem(7);

    @include mq("md") {
        position: relative;
        padding: 0 rem(20);



    }
}

.price__bg-img {
    display: none;

    @include mq("md") {
        display: block;
        position: absolute;
        top: 78%;
        left: 7%;
        width: rem(437);
    }
}




.price__main {
    max-width: rem(400);
    margin: 0 auto;
    margin-top: rem(30);
    margin-bottom: rem(43);

    @include mq("md") {
        display: flex;
        flex-direction: row-reverse;
        justify-content: center;
        max-width: 100%;
        width: rem(1080);
        margin-top: rem(40);
        margin-bottom: rem(80);

    }
}

.sp-price__img {
    max-width: rem(345);
    margin: 0 auto;

    @include mq("md") {
        display: none;
    }
}

.pc-price__img {
    display: none;

    @include mq("md") {
        display: block;
        max-width: rem(492);
        height: rem(745);
        width: 100%;
        padding-top: 66.571429%; //比率の計算式 横233/縦350*100%=画像の比率
        position: relative;
    }
}

.pc-price__img img {

    @include mq("md") {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.price__course {

    @include mq("md") {
        width: rem(492);
        margin-right: rem(90);

    }
}
.price__content  {
    margin-top: rem(40);
    @include mq("md") {
        margin-top: rem(0);
    }

}

.price__content +.price__content {

    @include mq("md") {
        margin-top: rem(40);
    }

}

.price__title {
    font-size: rem(16);
    font-weight: bold;
    border-bottom: rem(1) solid $green;
    padding-bottom: rem(2);
    margin-top: rem(0);

    @include mq("md") {
        margin-top: rem(0);
    }
}


.price__information {
    font-size: rem(14);

    justify-content: space-between;


}

.price__course-title {
    display: flex;
    justify-content: space-between;
}

.price__text {
    margin-top: rem(11);
    font-size: rem(14);

    @include mq("md") {
        margin-top: rem(9);
        font-size: rem(16);
        line-height: rem(24);
    }
}

.price__price {}

.price__section-title {
    width: rem(80);
    margin: 0 auto;

    @include mq("md") {
        width: rem(100);
    }
}

.price__section-text {
    font-size: rem(12);
    color: $green;
    text-align: center;
    margin-top: rem(15);

}

.price__subtext {
    margin-top: rem(10);
    font-size: rem(14);

    letter-spacing: -0.1em;

    @include mq("md") {
        font-size: rem(16);
        line-height: rem(24);
    }
}


div.pc-price__img {
    position: relative;


    @include mq("md") {}
}

div.pc-price__img div.color {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    background-color: #10404c;
}

div.pc-price__img img {
    display: block;
    width: 100%;
}



dl.price__list {
    display: flex;
    justify-content: space-between;
   margin-top: rem(8);
}

.price__name {
    font-size: rem(14);

    @include mq("md") {
    
        font-size: rem(16);
        line-height: rem(24);
    }
}

.price__money {


    font-size: rem(14);

    letter-spacing: -0.1em;

    @include mq("md") {
        font-size: rem(16);
        line-height: rem(24);
    }
}