@use "global" as *;


.information {
    background-color: $green2;
    padding-top: rem(75);
    padding-bottom: rem(66);
    margin-top: rem(67);


    @include mq("md") {
        padding-bottom: rem(93);
        margin-top: rem(140);
        padding-top: rem(100);

    }
}

.information__title-inner {

    margin: 0 auto;


    @include mq("md") {
    
        margin: 0 auto;
       

    }
}

.information__section-title {
    width: rem(189);
    margin: 0 auto;
    font-weight: bold;

    @include mq("md") {
        width: rem(236);
        padding-top: rem(0);
        padding-bottom: rem(0);

    }
}

.information__section-text {
    font-size: rem(12);
    color: $green;
    text-align: center;
    margin-top: rem(10);

    @include mq("md") {
        margin-top: rem(15);
    }
}

.space__information-head {
    padding-top: rem(85);

    @include mq("md") {
        padding-top: rem(102);
    }
}

.space__information-foot {
    padding-bottom: rem(0);

    @include mq("md") {
        padding-bottom: rem(10);
    }
}

.information__main {
    max-width: rem(600);
    margin: 0 auto;
    margin-top: rem(30);

    @include mq("md") {
        display: flex;
        justify-content: center;
        max-width: rem(1080);
        margin-top: rem(40);
    }
}

.information__img {
    max-width: rem(345);
    width: 100%;
    height: rem(227);
  
position: relative;
    @include mq("md") {
        max-width: rem(540);
        height: rem(356);
    }
}

.information__img img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
    height: 100%;
    object-fit: cover;
    @include mq("md") {

    }
}
.information__group {

    @include mq("md") {
        max-width: rem(492);
        margin-top: rem(40);
   
    }
}

.information__title {
    font-size: rem(20);
    font-weight: bold;
    color: $black2;
    border-bottom: rem(1) solid $black2;
    margin-top: rem(23);
    font-weight: bold;

    @include mq("md") {
        font-size: rem(24);
       padding-bottom: rem(4);
        line-height: 1em;
        
 
    }
}

.information__text {
    font-size: rem(16);
    font-weight: 500;
    line-height: rem(24);
    margin-top: rem(5);
    font-weight: 400;
    margin-bottom: rem(42);

    @include mq("md") {
        margin-bottom: rem(0);
        margin-top: rem(10);
    }

}

.c-space__information-button-head {
    padding-top: rem(32);

    @include mq("md") {
        padding-top: rem(30);
    }

}

.c-space__information-button-foot {
    padding-top: rem(59);

    @include mq("md") {
        padding-top: rem(80);
    }
}

div.information__img {
    position: relative;
    width: 100%;
  max-width: 100%;
    margin-top: rem(0);

    @include mq("md") {
        margin-top: rem(0);
        position: relative;
        max-width: rem(540);
        margin-right: rem(48);
    
    }

}

.information__img::before {
    content: "";
    display: block;
    padding-top: 65.5925926%;//比率管理の値=高さ/幅
  
  }

  .information__img img  {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
  }

div.information__img .color {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    background-color: $green;
}

div.information__img img {
    display: block;
    width: 100%;
}