@use "global" as *;

.main-mv {
  position: relative;
  overflow: hidden;
  object-fit: cover;

  @include mq("md") {
  
  }
}

.main-mv__text {
  position: absolute;

  color: $white;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  @include mq("md") {
    left: 50%;
    top: 50%;
    transform: translate(-45%, -25%);
  

  }
}

.main-mv__title {
  font-size: rem(40);
  letter-spacing: 0.54em;
  line-height: 1;
text-indent: 0.55em;

  @include mq("md") {
    font-size: rem(80);
    letter-spacing: 0.55em;
   
   

  }
}

.main-mv__subtitle {
  font-size: rem(20);
  text-align: center;

  @include mq("md") {
    padding-top: rem(10);
   

  }
}


.sp-slide-items {
  display: inline-block;
  width: 100%;

  @include mq("md") {
    display: none;
  }
}




.pc-slide-items {
  display: none;
  width: 100%;

  @include mq("md") {
    display: block;
  }
}

.slide-items img {
  max-width: 100%;
  
}


.slick-slide img {
  height: 100vh;
  @include mq("md") {
    display: block;
  
  }
}



//スライド
.slide {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.slide-image {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  opacity: 0;
  animation: slider-1 24s linear infinite;
}

.slide-image:nth-child(1) {
  background-image: url("/assets/images/top/TOP-MV(4).png");

  animation-delay: -2s;
}

.slide-image:nth-child(2) {
  background-image: url("/assets/images/top/TOP-MV(2).png");
  animation-delay: 6s;
}

.slide-image:nth-child(3) {
  background-image: url("/assets/images/top/TOP-MV(3).png");
  animation-delay: 14s;
}

.slide-image:nth-child(3) {
  background-image: url("/assets/images/top/TOP-MV(1).png");
  animation-delay: 14s;
}

@keyframes slider-1 {
  0% {
    opacity: 0;
    transform: scale(1);
  }
  4.16% {
    opacity: 1;
  }
  33.33% {
    opacity: 1;
  }
  41.66% {
    opacity: 0;
    transform: scale(1.2);
  }
  100% {
    opacity: 0;
  }
}